<template>
  <div class="row row-cols-1 row-cols-md-2 g-3">
    <div class="col">
      <div class="card">
        <div
          class="card-header bg-white bg-opacity-10"
          @click="showcardbody[0] = !showcardbody[0]"
          :class="{ 'remove-border-bottom-for-card': !showcardbody[0] }"
        >
          About token
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item" v-if="showcardbody[0]">
            Token is use for the api to get SEA In Game
            <br />
            <span class="text-danger"
              >* not sure will get baned or not (use at your own risk)</span
            >
          </li>
          <li
            class="list-group-item bg-white bg-opacity-10"
            v-if="showcardbody[0]"
          >
            Method 2 Example - The format of text to input
          </li>
          <li
            class="list-group-item"
            v-if="showcardbody[0]"
            title="Double Click To Copy"
            @dblclick="
              copyTextToClipboard(
                'NTF 0x416f1D70c1C22608814d9f36c492EfB3Ba8cad4c\nSEA 0x26193C7fa4354AE49eC53eA2cEBC513dc39A10aa\nSSS 0xC3028FbC1742a16A5D69dE1B334cbce28f5d7EB3'
              )
            "
          >
            NTF 0x416f1D70c1C22608814d9f36c492EfB3Ba8cad4c<br />
            SEA 0x26193C7fa4354AE49eC53eA2cEBC513dc39A10aa<br />
            SSS 0xC3028FbC1742a16A5D69dE1B334cbce28f5d7EB3
          </li>
          <li
            class="list-group-item bg-white bg-opacity-10"
            v-if="showcardbody[0]"
          >
            Method 3 Tutorial - How to get the file to import accounts
          </li>
          <li class="list-group-item" v-if="showcardbody[0]">
            <ul style="list-style-type: decimal">
              <li>
                Drag the button at below to bookmark bar.<br />
                <a :href="link" class="btn btn-secondary"
                  >Fetch Account From StarSharks</a
                >
              </li>
              <li>
                Use the bookmark added at 1st step on starshark official home
                page.
              </li>
            </ul>
            <span class="text-danger">
              * The bookmark will only get the accounts that connected before.
            </span>
          </li>
        </ul>
      </div>
    </div>
    <div class="col">
      <div class="card">
        <div
          class="card-header bg-white bg-opacity-10"
          @click="showcardbody[1] = !showcardbody[1]"
        >
          Method 1 - Add Address (no token)<br />
          <span class="text-danger"
            >* cant get game sea amount with this method</span
          >
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item" v-show="showcardbody[1]">
            <div class="mb-3">
              <label for="address" class="form-label">Account Address</label>
              <input
                type="text"
                class="form-control"
                :value="address"
                @input="(event) => (address = event.target.value)"
              />
            </div>
            <div class="mb-3">
              <label for="name" class="form-label">Account Name</label>
              <input
                type="text"
                class="form-control"
                :value="name"
                @input="(event) => (name = event.target.value)"
              />
            </div>
            <div class="d-grid gap-2">
              <button
                type="button"
                class="btn btn-primary"
                @click="addAddress()"
              >
                Add Address
              </button>
            </div>
            <div
              class="alert mb-0 mt-3"
              role="alert"
              :class="{
                hide: addAlert == -1,
                'alert-warning': addAlert == 0,
                'alert-success': addAlert == 1,
              }"
            >
              {{ addAlertText }}
            </div>
          </li>
          <li
            class="list-group-item bg-white bg-opacity-10"
            @click="showcardbody[4] = !showcardbody[4]"
            :class="{ 'remove-border-bottom-for-card': !showcardbody[4] }"
          >
            Method 2 - Import Multiple Address With Text (no token)
          </li>
          <li class="list-group-item" v-show="showcardbody[4]">
            <div class="mb-3">
              <label for="textarea-address" class="form-label">Address</label>
              <Codemirror
                v-model="cmAddress"
                height="200"
                :options="cmOptions"
                @change="cmAddressChange"
                name="test"
              />
            </div>
            <div
              class="alert mb-3 mt-3"
              role="alert"
              :class="{
                hide: addAlert2Info == -1,
                'alert-info': addAlert2Info == 0,
              }"
              v-html="addAlertText2Info"
            ></div>
            <div class="d-grid gap-2">
              <button
                type="button"
                class="btn btn-primary"
                @click="addAddress2()"
              >
                Add Address
              </button>
            </div>
            <div
              class="alert mb-0 mt-3"
              role="alert"
              :class="{
                hide: addAlert2 == -1,
                'alert-warning': addAlert2 == 0,
                'alert-success': addAlert2 == 1,
              }"
            >
              {{ addAlertText2 }}
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="col order-md-3">
      <div class="card">
        <div
          class="card-header bg-white bg-opacity-10"
          @click="showcardbody[2] = !showcardbody[2]"
          :class="{ 'remove-border-bottom-for-card': !showcardbody[2] }"
        >
          Method 3 - Import Address (has token)
        </div>
        <div class="card-body" v-show="showcardbody[2]">
          <div
            id="upload-container"
            @dragover="dragoverFile"
            @drop="dropFile"
            @click="$refs.iFile.click()"
          >
            <i class="fa-solid fa-cloud-upload"></i>
            <p aria-hidden="true" id="choose">
              <strong>Choose a the file that fetch from bookmark </strong>
            </p>
            <p aria-hidden="true">or drag and drop to import</p>
            <input type="file" @change="inputFile" :value="file" ref="iFile" />
          </div>
        </div>
      </div>
    </div>
    <div class="col order-md-2">
      <div class="card">
        <div
          class="card-header bg-white bg-opacity-10"
          @click="showcardbody[3] = !showcardbody[3]"
          :class="{ 'remove-border-bottom-for-card': !showcardbody[3] }"
        >
          Accounts List
        </div>
        <ul class="list-group list-group-flush" v-show="showcardbody[3]">
          <li
            class="list-group-item text-center"
            v-if="savedAddress.length == 0"
          >
            No account.
          </li>
          <li
            class="list-group-item"
            v-for="(sa, index) in savedAddress"
            :key="sa.address"
          >
            <div class="account-header d-flex justify-content-between">
              <div class="account-name" v-show="editName[index]">
                {{ index + 1 }}. {{ sa.name }}
                <i
                  class="fa-solid fa-pen-to-square fa-fw"
                  @click="(editName[index] = false), focusEditName(index)"
                ></i>
              </div>
              <div class="account-name-edit" v-show="!editName[index]">
                {{ index + 1 }}.
                <input
                  type="text"
                  :value="editNameText[index]"
                  @blur="
                    (event) => {
                      (editName[index] = true),
                        updateName(index, event.target.value);
                    }
                  "
                  @keyup.enter="
                    (event) => {
                      (editName[index] = true),
                        (editNameText[index] = event.target.value);
                    }
                  "
                  ref="editName"
                />
              </div>
              <div class="account-button d-flex">
                <div
                  class="move-up"
                  title="Move Up"
                  v-show="index != 0"
                  @click="moveUpAddress(sa.address)"
                >
                  <i class="fa-solid fa-angles-up fa-fw"></i>
                </div>
                <div
                  class="move-down"
                  title="Move Down"
                  v-show="index != savedAddress.length - 1"
                  @click="moveDownAddress(sa.address)"
                >
                  <i class="fa-solid fa-angles-down fa-fw"></i>
                </div>
                <div
                  class="remove"
                  title="Remove"
                  @click="removeAddress(sa.address, sa.name)"
                >
                  <i class="fa-solid fa-xmark fa-fw"></i>
                </div>
              </div>
            </div>
            <div class="account-body">
              <div class="account-address">
                <span class="spanaddr"
                  ><a
                    :href="`https://bscscan.com/address/${sa.address}`"
                    target="_blank"
                    >{{ sa.address }}</a
                  ></span
                >
              </div>
              <div class="account-jwt-info d-flex justify-content-between">
                <div>{{ checkSAToken(sa) }}</div>
                <div
                  v-if="sa.authorization !== undefined"
                  class="text-decoration-underline"
                  @click="removeToken(index, sa)"
                >
                  Remove Token
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import { copyTextToClipboard, JWTdecode } from "@/assets/js/function";
import Codemirror from "codemirror-editor-vue3";

export default {
  name: "AccountsView",
  components: {
    Codemirror,
  },
  data() {
    return {
      address: "",
      name: "",
      addAlert: -1,
      addAlertText: "",
      addTimeout: null,
      addAlert2: -1,
      addAlertText2: "",
      addTimeout2: null,
      addAlert2Info: -1,
      addAlertText2Info: "",
      file: "",
      link: 'javascript:(t=>{var e=document.createElement("a");e.setAttribute("href","data:text/json;charset=utf-8,"+encodeURIComponent(t)),e.setAttribute("download","starshark_monitor_import_data"),e.style.display="none",document.body.appendChild(e),e.click(),document.body.removeChild(e)})(localStorage.SESSIONS);',
      showcardbody: [true, true, true, true, true],
      editName: [],
      editNameText: [],
      cmOptions: {
        lineNumbers: true,
        smartIndent: false,
      },
      cmAddress: "",
      cmWidgets: [],
    };
  },
  computed: {
    savedAddress() {
      return this.$store.getters.savedAddressDecode;
    },
  },
  mounted() {
    this.loadEditName(this.savedAddress);
  },
  watch: {
    savedAddress(data) {
      this.loadEditName(data);
    },
  },
  methods: {
    copyTextToClipboard(text) {
      copyTextToClipboard(text);
    },
    checkCmAddress(address) {
      let tmpRow = address.split("\n");
      let row = [],
        errors = [];
      for (const i in tmpRow) {
        let tmp = tmpRow[i].trim().replaceAll("\t", " ").split(" ");
        let address,
          name = "",
          hasAddress = false;
        while (tmp.length > 0) {
          address = tmp.pop().toLowerCase();
          if (this.StarSharks.web3.utils.isAddress(address)) {
            hasAddress = true;
            break;
          }
        }
        if (hasAddress) {
          name = tmp.filter((d) => d !== "").join(" ");
          row.push([name, address]);
        } else {
          errors.push(i);
        }
      }
      return {
        data: row,
        errors: tmpRow.length == 1 && tmpRow[0] == "" ? [] : errors,
      };
    },
    cmAddressChange(value, editor) {
      this.cmAddress = value;
      let data = this.checkCmAddress(this.cmAddress);
      editor.operation(() => {
        for (var i = 0; i < this.cmWidgets.length; ++i)
          editor.removeLineWidget(this.cmWidgets[i]);
        this.cmWidgets = [];

        for (const i of data.errors) {
          var msg = document.createElement("div");
          var icon = msg.appendChild(document.createElement("span"));
          icon.innerHTML = "!!";
          icon.className = "lint-error-icon";
          msg.appendChild(document.createTextNode("No Address Detected."));
          msg.className = "lint-error text-danger";
          this.cmWidgets.push(
            editor.addLineWidget(parseInt(i), msg, {
              coverGutter: false,
              noHScroll: true,
            })
          );
        }
      });
      let text = [];
      if (data.data.length > 0) {
        text.push(`Total ${data.data.length} address to add.`);
      }
      if (data.errors.length > 0) {
        text.push(`There is ${data.data.length} line can't be read.`);
      }
      text = text.join("<br>");
      this.setAddAlert2Info(text, text !== "" ? 0 : -1);
    },
    focusEditName(index) {
      setTimeout(() => {
        this.$refs.editName[index].focus();
      }, 100);
    },
    loadEditName(data) {
      this.editName = [];
      this.editNameText = [];
      for (let i = 0; i < data.length; i++) {
        this.editName.push(true);
        this.editNameText.push(data[i].name);
      }
    },
    updateName(index, name) {
      let data = this.savedAddress[index];
      if (data.name != name) {
        data.name = name;
        this.$store.commit("updateSA", {
          index: index,
          data: data,
        });
        this.editNameText[index] = name;
      }
    },
    addAddress() {
      let SA = this.$store.state.savedAddress;
      if (
        this.StarSharks.web3.utils.isAddress(this.address) &&
        SA.map((d) => d.address.toLowerCase()).indexOf(
          this.address.toLowerCase()
        ) === -1
      ) {
        this.$store.commit("addSA", {
          address: this.address.toLowerCase(),
          name: this.name,
        });
        this.setAddAlert("Success add account");
        this.address = "";
        this.name = "";
      } else {
        if (!this.StarSharks.web3.utils.isAddress(this.address)) {
          this.setAddAlert("Account format wrong", 0);
        } else if (
          SA.map((d) => d.address.toLowerCase()).indexOf(
            this.address.toLowerCase()
          ) !== -1
        ) {
          this.setAddAlert("Account exist", 0);
        }
      }
    },
    addAddress2() {
      let SA = this.$store.state.savedAddress;
      let addrs = this.checkCmAddress(this.cmAddress).data;
      let rst = { d: 0, e: 0 };
      for (const addr of addrs) {
        if (SA.map((d) => d.address.toLowerCase()).indexOf(addr[1]) === -1) {
          this.$store.commit("addSA", {
            address: addr[1],
            name: addr[0],
          });
          rst.d++;
        } else {
          rst.e++;
        }
      }
      let text = [];
      if (rst.d > 0) text.push(`${rst.d} accounts added successfully.`);
      if (rst.e > 0) text.push(`${rst.e} account already exists.`);
      text = text.join("<br>");
      if (text !== "") this.setAddAlert2(text);
      else this.setAddAlert2("There is no account to add.", 0);
    },
    removeAddress(address, name) {
      if (
        confirm(
          `Are you sure you want to delete this account.\nName:     ${name}\nAddress: ${address}`
        )
      ) {
        this.$store.commit("removeSA", address);
      }
    },
    removeToken(index, sa) {
      if (
        confirm(
          `Are you sure you want to remove token from this account.\nName:     ${sa.name}\nAddress: ${sa.address}`
        )
      ) {
        this.$store.commit("updateSA", {
          index: index,
          data: {
            address: sa.address.toLowerCase(),
            name: sa.name,
          },
        });
      }
    },
    moveUpAddress(address) {
      this.$store.commit("moveUpSA", address);
    },
    moveDownAddress(address) {
      this.$store.commit("moveDownSA", address);
    },
    setAddAlert(text, type = 1) {
      clearTimeout(this.addTimeout);
      this.addAlertText = text;
      this.addAlert = type;
      if (type == 1) {
        this.addTimeout = setTimeout(() => {
          this.addAlert = -1;
        }, 3000);
      }
    },
    setAddAlert2(text, type = 1) {
      clearTimeout(this.addTimeout2);
      this.addAlertText2 = text;
      this.addAlert2 = type;
      if (type == 1) {
        this.addTimeout2 = setTimeout(() => {
          this.addAlert2 = -1;
        }, 3000);
      }
    },
    setAddAlert2Info(text, type = 0) {
      this.addAlertText2Info = text;
      this.addAlert2Info = type;
    },
    loadImportFile(file) {
      const reader = new FileReader();
      reader.addEventListener("load", async (event) => {
        let data = event.target.result;
        data = JSON.parse(data);
        for (const key in data) {
          let index = this.$store.state.savedAddress
            .map((d) => d.address.toLowerCase())
            .indexOf(key.toLowerCase());
          data[key] = JWTdecode(data[key]);
          if (
            data[key].authorization != undefined &&
            data[key].exp * 1000 > Date.now()
          ) {
            let accData = await this.StarSharks.getBaseAccData(
              data[key].authorization
            );
            accData = accData.data.data;
            if (index == -1) {
              this.$store.commit("addSA", {
                address: accData.account.toLowerCase(),
                name: accData.name,
                authorization: data[key].authorization,
                qr_code: data[key].qr_code,
              });
            } else {
              this.$store.commit("updateSA", {
                index: index,
                data: {
                  address: accData.account.toLowerCase(),
                  name: accData.name,
                  authorization: data[key].authorization,
                  qr_code: data[key].qr_code,
                },
              });
            }
          } else {
            if (index == -1) {
              this.$store.commit("addSA", {
                address: key,
                name: "",
              });
            }
          }
        }
      });
      try {
        reader.readAsText(file);
      } catch (error) {
        console.error(error);
      }
    },
    inputFile(event) {
      console.log(event.target.files);
      if (event.target.files.length > 0) {
        this.loadImportFile(event.target.files[0]);
        event.target.value = "";
      }
    },
    dragoverFile(event) {
      event.stopPropagation();
      event.preventDefault();
      event.dataTransfer.dropEffect = "copy";
    },
    dropFile(event) {
      event.stopPropagation();
      event.preventDefault();
      this.loadImportFile(event.dataTransfer.files[0]);
    },
    checkSAToken(sa) {
      if (sa.authorization == undefined) return "No token setup.";
      let exp = sa.exp * 1000;
      if (Date.now() >= exp) return "Token expired.";
      else return `Token will expire ${moment(exp).fromNow()}.`;
    },
  },
};
</script>

